.wrapper {
    /*height: 100vh;*/
    min-height: 100vh;
    box-shadow: 0 20px 10px -10px rgba(86, 230, 243, 0.45);
}

.container {
    height: 100%;
    width: 70%;
    margin: 0 auto;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: white;
}

.elements {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
@media screen and (max-width: 1200px) {
    .elements {
        justify-content: center;
    }
}